webpackJsonp([25572397053279],{

/***/ 428:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Hanamirb + Vue.js 시리즈 목차</strong></p>\n<ol>\n<li>\n<p><a href=\"/posts/hanami-vuejs-moneybook-api-server\">API 서버</a></p>\n</li>\n<li>\n<p><a href=\"/posts/hanami-vuejs-moneybook-frontend-1\">로그인 &#x26; 회원가입 페이지</a></p>\n</li>\n<li>\n<p><a href=\"/posts/hanami-vuejs-moneybook-frontend-2\">가계부 페이지</a></p>\n</li>\n</ol>\n<hr>\n<p><strong>업데이트</strong></p>\n<ul>\n<li>BCrypt 관련 설명 수정 (2017-05-02)</li>\n</ul>\n<hr>\n<h2 id=\"들어가며\"><a href=\"#%EB%93%A4%EC%96%B4%EA%B0%80%EB%A9%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>들어가며</h2>\n<p>하나미 정식 버전이 지난 4월 6일에 발표되었습니다. 기존에 레일즈에 여러모로 아쉬움을 느끼고 있던 해외의 루비 개발자들은 많은 관심을 가지고 있나봅니다. 제가 처음 하나미를 접했을 때 버전이 0.7.2였는데 어느새 1.0.0 버전이 발매되었고, 아직도 쓰고있다보니 감개무량합니다.</p>\n<p>요즘 프론트엔드 JS 프레임워크에 많은 관심을 가지고 ReactJS, Vue.js를 공부하고 있는데, 간단히 배운 내용을 적용해볼 겸 간단 가계부를 구현해보도록 하겠습니다. 백엔드 JSON API 서버는 하나미로 구현하고, 프론트엔드는 Vue.js로 구현하는 과정을 보여드리겠습니다.</p>\n<p>최대한 간단히 설명해드리려 노력하겠지만, 기존에 루비로 웹 개발을 해보신분이 이해하시기 쉬울 겁니다. 사실 JSON API 서버는 어떤 언어나 프레임워크로 제공해도 되니까요 :) 구현방법에 약간씩 차이가 있을 뿐이지 제가 보여드리는 정도의 앱은 그렇게 어렵지 않게 작성하실 수 있으리라 봅니다.</p>\n<h2 id=\"애플리케이션의-기본-구성\"><a href=\"#%EC%95%A0%ED%94%8C%EB%A6%AC%EC%BC%80%EC%9D%B4%EC%85%98%EC%9D%98-%EA%B8%B0%EB%B3%B8-%EA%B5%AC%EC%84%B1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>애플리케이션의 기본 구성</h2>\n<p>처음에는 더욱 간단한 To-Do List(할일 관리) 를 구현하려고 했는데, 그러면 하나미를 다루는 부분이 너무 영양가가 없어질 것 같아서 가계부 형식의 앱으로 만들고, 다음의 기능을 구상해 보았습니다.</p>\n<ul>\n<li>\n<p>회원 등록 및 인증</p>\n<ul>\n<li>이메일과 비밀번호로 회원 가입</li>\n<li>JWT로 인증 (토큰은 LocalStorage에 저장)</li>\n</ul>\n</li>\n<li>\n<p>사용금액, 내용 입력\u001c</p>\n<ul>\n<li>수입은 녹색, 지출은 빨강색으로 표시</li>\n<li>각 아이템을 클릭하면 바로 수정 가능, 삭제 버튼을 누르면 삭제</li>\n</ul>\n</li>\n<li>수입, 지출을 필터링하여 보기</li>\n<li>가장 최신 레코드 30개를 처음에 불러온 뒤, 화면에 보이는 마지막 내역까지 스크롤 하게 되면 추가로 자료 가져오기</li>\n</ul>\n<p>이 정도의 기능을 구현해볼 예정입니다. 막상 하려니 쉽진 않겠지만 차근차근 진행해보겠습니다.</p>\n<h2 id=\"하나미-프로젝트-생성\"><a href=\"#%ED%95%98%EB%82%98%EB%AF%B8-%ED%94%84%EB%A1%9C%EC%A0%9D%ED%8A%B8-%EC%83%9D%EC%84%B1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>하나미 프로젝트 생성</h2>\n<p>너무나 당연하지만 하나미를 사용하기 위해서는 개발 환경에 루비가 세팅되어 있어야 합니다. 아직 루비가 설치되어있지 않은 경우에는 <a href=\"https://gorails.com/setup/\">GoRails의 설치 가이드</a>를 따라하시면 되겠습니다. 그 다음에 하나미 젬을 설치하시면 됩니다. 이번 강좌에서는 <strong>루비 2.4.1</strong>, <strong>하나미 1.0.0</strong> 버전을 기준으로 프로젝트를 생성하겠습니다.</p>\n<p>먼저 하나미 젬을 설치합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>gem install hanami</code></pre>\n      </div>\n<p>설치 된 이후에 프로젝트는 원하시는 이름으로 설치하시면 됩니다. 하나미는 SQLite, MySQL, PostgreSQL 등을 지원하지만, 기본적으로 PostgreSQL을 권장합니다. 또한 테스팅 프레임워크도 처음 프로젝트를 설정할 때 Minitest, RSpec 중에서 고르실 수 있습니다. 제가 아직 RSpec은 커녕 TDD를 잘 못하기 때문에 Minitest로 진행해보겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>hanami new moneybook-api --database=postgres --test=minitest</code></pre>\n      </div>\n<p>생성된 프로젝트에서 젬파일을 살펴보시면 꽤 간단합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># Gemfile</span>\nsource <span class=\"token string\">'https://rubygems.org'</span>\n\ngem <span class=\"token string\">'rake'</span>\ngem <span class=\"token string\">'hanami'</span><span class=\"token punctuation\">,</span>       <span class=\"token string\">'~> 1.0'</span>\ngem <span class=\"token string\">'hanami-model'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'~> 1.0'</span>\n\ngem <span class=\"token string\">'pg'</span>\n\ngroup <span class=\"token symbol\">:development</span> <span class=\"token keyword\">do</span>\n  <span class=\"token comment\"># Code reloading</span>\n  <span class=\"token comment\"># See: http://hanamirb.org/guides/projects/code-reloading</span>\n  gem <span class=\"token string\">'shotgun'</span>\n<span class=\"token keyword\">end</span>\n\ngroup <span class=\"token symbol\">:test</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:development</span> <span class=\"token keyword\">do</span>\n  gem <span class=\"token string\">'dotenv'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'~> 2.0'</span>\n<span class=\"token keyword\">end</span>\n\ngroup <span class=\"token symbol\">:test</span> <span class=\"token keyword\">do</span>\n  gem <span class=\"token string\">'minitest'</span>\n  gem <span class=\"token string\">'capybara'</span>\n<span class=\"token keyword\">end</span>\n\ngroup <span class=\"token symbol\">:production</span> <span class=\"token keyword\">do</span>\n  <span class=\"token comment\"># gem 'puma'</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>이 중에서 <code>capybara</code> 젬은 뷰 테스트 용이기 떄문에 이번 프로젝트에선 굳이 없어도 됩니다. 앞으로 젬이 더 추가될테지만 필요에 따라 추가하기로 하고, <code>bundle install</code> 커맨드로 필요한 젬을 설치해두겠습니다.</p>\n<p>참고로 PostgreSQL을 이용하시려면 <code>.env</code> 파일에서 계정 설정을 해주셔야 합니다. SQLite를 이용하신다면 파일로 관리되기 때문에 계정 설정을 해 주실 필요는 없습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code># .env.development\nDATABASE_URL=\"postgresql://username:password@localhost/moneybook_api_development\"</code></pre>\n      </div>\n<h2 id=\"사용자-인증-구현\"><a href=\"#%EC%82%AC%EC%9A%A9%EC%9E%90-%EC%9D%B8%EC%A6%9D-%EA%B5%AC%ED%98%84\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>사용자 인증 구현</h2>\n<p>기존의 웹 페이지는 서버에서 인증 관련 정보를 들고, 세션을 이용하여 인증 상태를 유지하고 요청을 하는 경우가 많았습니다. 하지만 모바일 애플리케이션이나, 많은 인스턴스를 올려서 인증을 관리하는 경우 세션 기반 인증은 유연한 대응이 어렵습니다. JWT라는 개념을 이용하면 세션 기반 인증의 단점을 극복하고 사용자 인증을 적용할 수 있습니다.</p>\n<p>JWT 인증이 무엇인지 궁금하시다면 다음의 링크를 읽어보시길 바랍니다.</p>\n<ul>\n<li><a href=\"https://velopert.com/2389\">JSON Web Token 소개 및 구조 - velopert</a></li>\n<li><a href=\"http://bcho.tistory.com/999\">REST JWT 소개 - 조대협</a></li>\n<li><a href=\"https://jwt.io/introduction/\">Introduction to JSON Web Tokens</a></li>\n</ul>\n<p>인증에 필요한 젬을 추가한 뒤, 사용자 정보를 기록하는 모델을 생성하겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># Gemfile</span>\n<span class=\"token comment\"># ...</span>\ngem <span class=\"token string\">'bcrypt'</span>\ngem <span class=\"token string\">'jwt'</span>\n<span class=\"token comment\"># ...</span>\n</code></pre>\n      </div>\n<p><code>bcrypt</code> 젬은 비밀번호 생성에 특화된 단방향 해시 함수를 지원합니다.<br>\n간단한 사용법을 알려드리면 <code>BCrypt::Password.create('password')</code> 로 해시 다이제스트를 생성하고, <code>BCrypt::Password.new(digest)</code> 로 해시 다이제스트가 본래 비밀번호와 일치하는지 검사합니다.</p>\n<p>너무나 당연하면서 중요한 이야기지만 웹 개발자로서 복잡한 보안 알고리즘을 직접 구현하면 관리 및 취약점을 파악하기가 어렵기 때문에 차라리 <code>bcrypt</code> 처럼 검증된 라이브러리를 사용하시길 추천합니다. 비슷한 예로 Rails에서 사용자 인증과 관련한 부분은 되도록이면 Devise 젬을 사용하길 권장하는 것 처럼요.</p>\n<p>패스워드 저장 매커니즘에 대해 <a href=\"http://d2.naver.com/helloworld/318732\">이 글</a>을 읽어보시길 추천합니다.</p>\n<h3 id=\"사용자-모델-생성\"><a href=\"#%EC%82%AC%EC%9A%A9%EC%9E%90-%EB%AA%A8%EB%8D%B8-%EC%83%9D%EC%84%B1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>사용자 모델 생성</h3>\n<p>먼저 사용자 정보를 저장하기 위한 모델을 생성하겠습니다. 간단히 이메일과 패스워드를 저장하는 테이블을 만들면 되겠네요.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>bundle exec hanami generate model user</code></pre>\n      </div>\n<p>앞으로 모든 하나미 커맨드 앞에는 <code>bundle exec</code>  이 붙을 예정입니다. 앞으로 하나미를 자주 쓰게 되면 <code>bashrc</code> 나  <code>zshrc</code> 에 alias 지정을 해 두는걸 추천합니다.</p>\n<p>그리고 레일즈와 비슷하게 <code>db/migrations</code> 폴더에 마이그레이션 파일이 있습니다. 어떤 컬럼에 어떤 정보가 들어가게 될 지 작성해주어야 합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># db/migration/2017***_create_users.rb</span>\n<span class=\"token constant\">Hanami</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Model</span><span class=\"token punctuation\">.</span>migration <span class=\"token keyword\">do</span>\n  change <span class=\"token keyword\">do</span>\n    create_table <span class=\"token symbol\">:users</span> <span class=\"token keyword\">do</span>\n      primary_key <span class=\"token symbol\">:id</span>\n\n      column <span class=\"token symbol\">:email</span><span class=\"token punctuation\">,</span> <span class=\"token builtin\">String</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n      column <span class=\"token symbol\">:password_digest</span><span class=\"token punctuation\">,</span> <span class=\"token builtin\">String</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n\n      column <span class=\"token symbol\">:created_at</span><span class=\"token punctuation\">,</span> <span class=\"token constant\">DateTime</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n      column <span class=\"token symbol\">:updated_at</span><span class=\"token punctuation\">,</span> <span class=\"token constant\">DateTime</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>방법에 따라서는 이메일 컬럼만 있어도 충분히 JWT 인증을 구현할 수 있다는데 아직 잘 모르겠습니다. 이제 테이블 생성을 위해 마이그레이션을 실행하겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>bundle exec hanami db create # 먼저 데이터베이스 생성부터\nbundle exec hanami db migrate</code></pre>\n      </div>\n<h3 id=\"사용자-등록-액션\"><a href=\"#%EC%82%AC%EC%9A%A9%EC%9E%90-%EB%93%B1%EB%A1%9D-%EC%95%A1%EC%85%98\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>사용자 등록 액션</h3>\n<p>이제 사용자를 등록하는 액션을 생성하겠습니다. 이후에 JWT 인증하는 과정을 덧붙일 예정입니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>bundle exec hanami generate action api auth#sign_in --skip-view\nbundle exec hanami generate action api auth#sign_up --skip-view</code></pre>\n      </div>\n<p>action 뒤에 <code>api\u001d</code> 는 제가 사용할 앱 이름입니다. 기본값은 web으로 되어 있지만, <a href=\"http://hanamirb.org/guides/command-line/generators/\">하나미 가이드의 Command Line 부분을 참고하시면</a> 다른 앱을 생성하거나 삭제하는 등 액션을 실행할 수 있습니다.</p>\n<p><code>--skip-view</code> 는 뷰와 관련된 템플릿 파일이나 뷰 클래스 및 테스트를 생성하지 않는 커맨드입니다. 우리는 하나미를 순순히 API 서버로 사용할 것이기 때문에 미리 입력해주어야 불필요한 파일을 만들지 않게 됩니다.</p>\n<p>라우팅 파일의 내용도 변경해줍니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/config/routes.rb</span>\npost <span class=\"token string\">'/auth/sign_in'</span><span class=\"token punctuation\">,</span> to<span class=\"token punctuation\">:</span> <span class=\"token string\">'auth#sign_in'</span>\npost <span class=\"token string\">'/auth/sign_up'</span><span class=\"token punctuation\">,</span> to<span class=\"token punctuation\">:</span> <span class=\"token string\">'auth#sign_up'</span>\n</code></pre>\n      </div>\n<p>사용자를 등록하는 과정은 JWT랑은 관련이 없습니다. 단순히 이메일, 비밀번호, 비밀번호 확인을 하는 패러매터를 전달 받은 뒤, 유효성 검사에 성공하면 비밀번호를 암호화하여 데이터베이스에 저장하면 됩니다.</p>\n<p>우리는 JSON API 서버를 만들고 있기 때문에 애플리케이션의 <code>body_parsers</code> 나 <code>default_request_format</code> 등의 설정을 바꾸어 주어야 합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/application.rb</span>\n<span class=\"token comment\"># From Line 90</span>\n      <span class=\"token comment\"># Default format for the requests that don't specify an HTTP_ACCEPT header</span>\n      <span class=\"token comment\"># Argument: A symbol representation of a mime type, defaults to :html</span>\n      <span class=\"token comment\">#</span>\n      default_request_format <span class=\"token symbol\">:json</span>\n\n      <span class=\"token comment\"># Default format for responses that don't consider the request format</span>\n      <span class=\"token comment\"># Argument: A symbol representation of a mime type, defaults to :html</span>\n      <span class=\"token comment\">#</span>\n      default_response_format <span class=\"token symbol\">:json</span>\n\n      <span class=\"token comment\"># HTTP Body parsers</span>\n      <span class=\"token comment\"># Parse non GET responses body for a specific mime type</span>\n      <span class=\"token comment\"># Argument: Symbol, which represent the format of the mime type</span>\n      <span class=\"token comment\">#             (only `:json` is supported)</span>\n      <span class=\"token comment\">#           Object, the parser</span>\n      <span class=\"token comment\">#</span>\n      body_parsers <span class=\"token symbol\">:json</span>\n<span class=\"token comment\"># ...</span>\n</code></pre>\n      </div>\n<p>이제 안정적으로 기능을 구현하기 위해 테스트를 작성하겠습니다. 테스트 파일은 <code>spec/</code> 폴더에 있습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># spec/api/controllers/auth/sign_up_spec.rb</span>\n<span class=\"token keyword\">require</span> <span class=\"token string\">'spec_helper'</span>\nrequire_relative <span class=\"token string\">'../../../../apps/api/controllers/auth/sign_up'</span>\n\ndescribe <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Auth</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:SignUp</span> <span class=\"token keyword\">do</span>\n  <span class=\"token function\">let</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:action</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Auth</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:SignUp</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span> <span class=\"token punctuation\">}</span>\n  <span class=\"token function\">let</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:params</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token punctuation\">{</span>\n      user<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n        email<span class=\"token punctuation\">:</span> <span class=\"token string\">\"foo@bar.com\"</span><span class=\"token punctuation\">,</span>\n        password<span class=\"token punctuation\">:</span> <span class=\"token string\">\"secret\"</span><span class=\"token punctuation\">,</span>\n        password_confirmation<span class=\"token punctuation\">:</span> <span class=\"token string\">\"secret\"</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n\n  describe <span class=\"token string\">'with valid params'</span> <span class=\"token keyword\">do</span>\n    it <span class=\"token string\">'is successful'</span> <span class=\"token keyword\">do</span>\n      response <span class=\"token operator\">=</span> action<span class=\"token punctuation\">.</span><span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      response<span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span>must_equal <span class=\"token number\">201</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n\n  describe <span class=\"token string\">'with invalid params'</span> <span class=\"token keyword\">do</span>\n    it <span class=\"token string\">'returns 422'</span> <span class=\"token keyword\">do</span>\n      response <span class=\"token operator\">=</span> action<span class=\"token punctuation\">.</span><span class=\"token function\">call</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n      response<span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">.</span>must_equal <span class=\"token number\">422</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>먼저 유효성 검사부터 만들겠습니다. 하나미의 유효성 검사는 모델에서 수행하는 게 아니라 각 액션에서 수행합니다. (공통된 유효성 검사를 모듈화할 수도 있습니다)</p>\n<p><strong>작성되는 코드는 모두 테스트를 먼저 작성하고 진행하지만, 글에서 모두 보여드리지 않겠습니다. 자세한 내용은 <a href=\"https://github.com/emaren84/moneybook_api\">Github에 올라와 있는 소스코드</a>를 살펴봐 주세요.</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/auth/sign_up.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Auth</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">SignUp</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n\n    <span class=\"token constant\">VALID_EMAIL_REGEX</span> <span class=\"token operator\">=</span> <span class=\"token regex\">/\\A[\\w+\\-.]+@[a-z\\d\\-]+(\\.[a-z\\d\\-]+)*\\.[a-z]+\\z/i</span>\n\n    params <span class=\"token keyword\">do</span>\n      <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:user</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>schema <span class=\"token keyword\">do</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:email</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">,</span> format<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> <span class=\"token constant\">VALID_EMAIL_REGEX</span><span class=\"token punctuation\">)</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:password</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>confirmation\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      halt <span class=\"token number\">422</span> <span class=\"token keyword\">unless</span> params<span class=\"token punctuation\">.</span>valid<span class=\"token operator\">?</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p><code>halt</code> 메서드는 인자로 받은 스테이터스 번호와 메세지를 리턴합니다. 메세지는 생략 가능합니다. 이제 올바른 패러매터가 들어왔으면  사용자를 등록하면 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># same file</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Auth</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">SignUp</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n\n    <span class=\"token constant\">VALID_EMAIL_REGEX</span> <span class=\"token operator\">=</span> <span class=\"token regex\">/\\A[\\w+\\-.]+@[a-z\\d\\-]+(\\.[a-z\\d\\-]+)*\\.[a-z]+\\z/i</span>\n\n    params <span class=\"token keyword\">do</span>\n      <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:user</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>schema <span class=\"token keyword\">do</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:email</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">,</span> format<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> <span class=\"token constant\">VALID_EMAIL_REGEX</span><span class=\"token punctuation\">)</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:password</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>confirmation\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">initialize</span><span class=\"token punctuation\">(</span>interactor<span class=\"token punctuation\">:</span> <span class=\"token constant\">UserSignUp</span><span class=\"token punctuation\">)</span>\n      <span class=\"token variable\">@interactor</span> <span class=\"token operator\">=</span> interactor\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      halt <span class=\"token number\">422</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Invalid Parameters'</span> <span class=\"token keyword\">unless</span> params<span class=\"token punctuation\">.</span>valid<span class=\"token operator\">?</span>\n\n      result <span class=\"token operator\">=</span> <span class=\"token variable\">@interactor</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:user</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>call\n      status <span class=\"token number\">201</span><span class=\"token punctuation\">,</span> result<span class=\"token punctuation\">.</span>user<span class=\"token punctuation\">.</span>show_info<span class=\"token punctuation\">.</span>to_json\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>뭔가 이상한 녀석이 나왔습니다. interactor라뇨? 인터렉터(Interactor)는 하나미에서 제공하는 모듈인데, 보통 다양한 비지니스 처리 로직을 분리하는 용도 혹은 재사용 가능한 비지니스 로직을 API, Web 같이 서로 다른 앱에서도 공유할 수 있도록 만들어졌습니다. <code>lib/interactor</code> 에 해당 클래스명으로 작성합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># lib/interactor/user_sign_up.rb</span>\n<span class=\"token keyword\">require</span> <span class=\"token string\">\"hanami/interactor\"</span>\n<span class=\"token keyword\">require</span> <span class=\"token string\">\"bcrypt\"</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">UserSignUp</span>\n  include <span class=\"token constant\">Hanami</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Interactor</span>\n  include <span class=\"token constant\">BCrypt</span>\n\n  expose <span class=\"token symbol\">:user</span>\n\n  <span class=\"token keyword\">def</span> <span class=\"token function\">initialize</span><span class=\"token punctuation\">(</span>user_attr<span class=\"token punctuation\">,</span> encryptor<span class=\"token punctuation\">:</span> <span class=\"token keyword\">nil</span><span class=\"token punctuation\">,</span> repo<span class=\"token punctuation\">:</span> <span class=\"token keyword\">nil</span><span class=\"token punctuation\">)</span>\n    <span class=\"token variable\">@user_attr</span> <span class=\"token operator\">=</span> user_attr\n    <span class=\"token variable\">@encryptor</span> <span class=\"token operator\">=</span> encryptor <span class=\"token operator\">||</span> <span class=\"token constant\">BCrypt</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Password</span>\n    <span class=\"token variable\">@repo</span> <span class=\"token operator\">=</span> repo <span class=\"token operator\">||</span> <span class=\"token constant\">UserRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span>\n  <span class=\"token class-name\">end</span>\n\n  <span class=\"token keyword\">def</span> call\n    safe_user <span class=\"token operator\">=</span> <span class=\"token function\">encrypt_user</span><span class=\"token punctuation\">(</span>user_attr<span class=\"token punctuation\">)</span>\n    <span class=\"token variable\">@user</span> <span class=\"token operator\">=</span> <span class=\"token variable\">@repo</span><span class=\"token punctuation\">.</span><span class=\"token function\">create</span><span class=\"token punctuation\">(</span>safe_user<span class=\"token punctuation\">)</span>\n  <span class=\"token keyword\">end</span>\n\n  private\n\n  attr_reader <span class=\"token symbol\">:user_attr</span>\n\n  <span class=\"token keyword\">def</span> <span class=\"token function\">encrypt_user</span><span class=\"token punctuation\">(</span>attributes<span class=\"token punctuation\">)</span>\n    password <span class=\"token operator\">=</span> <span class=\"token variable\">@encryptor</span><span class=\"token punctuation\">.</span><span class=\"token function\">create</span><span class=\"token punctuation\">(</span>attributes<span class=\"token punctuation\">[</span><span class=\"token symbol\">:password</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span>\n\n    <span class=\"token punctuation\">{</span> email<span class=\"token punctuation\">:</span> user_attr<span class=\"token punctuation\">[</span><span class=\"token symbol\">:email</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> password_digest<span class=\"token punctuation\">:</span> password <span class=\"token punctuation\">}</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>눈여겨 보실점은 <code>expose</code> 밖에 없고 나머지는 일반적인 사용자 등록 로직으로 구성되어 있습니다. 인터렉터는 해당 클래스의 객체를 생성하고 <code>call</code> 메서드로 연산을 수행하는데, 그 결과값을 <code>expose</code> 로 내보내야 합니다. 참고로 <code>expose</code> 로 내보내려는 값은 인스턴스 변수여야 합니다. 여기서는 <code>@user</code> 가 되겠네요. 내보낸 결과물은 메서드처럼 <code>.user</code> 로 가져올 수 있습니다.</p>\n<p>그런데 액션 부분에 <code>result.user.show_info.to_json</code>  이라고 되어있는 부분이 있습니다. <code>to_json</code> 은 리턴값이 JSON이어야 되니까 그렇다 치고, <code>show_info</code> 는 어디에 있는 녀석일까요? 방금까지 우리는 사용자를 생성했습니다. 그리고 생성된 결과값이 User Entity(엔티티)인데, 엔티티에 메서드를 정의하여 원하는 정보를 가공하여 보여줄 수 있도록 만든 것입니다. 콘솔에서 한번 확인해 보겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>bundle exec hanami console</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>[2] pry(main)> UserRepository.new.create(email: 'foo@baz.com', password_digest: 'some secret')\n[moneybook_api] [INFO] [2017-04-22 14:06:37 +0900] (0.003739s) INSERT INTO \"users\" (\"email\", \"password_digest\", \"created_at\", \"updated_at\") VALUES ('foo@baz.com', 'some secret', '2017-04-22 05:06:37.222355+0000', '2017-04-22 05:06:37.222355+0000') RETURNING \"id\", \"email\", \"password_digest\", \"created_at\", \"updated_at\"\n=> #<User:0x007fb6a6c49348\n @attributes=\n  {:id=>3, :email=>\"foo@baz.com\", :password_digest=>\"some secret\", :created_at=>2017-04-22 05:06:37 UTC, :updated_at=>2017-04-22 05:06:37 UTC}></code></pre>\n      </div>\n<p>저렇게 임의의 사용자를 생성하니 User 엔티티가 리턴된 것을 볼 수 있습니다. Repository가 뭐고, Entity가 무엇인지는 <a href=\"https://emaren84.github.io/blog/archivers/hanami-introduction#%EB%AA%A8%EB%8D%B8-%EA%B5%AC%EC%B6%95%ED%95%98%EA%B8%B0\">제가 전에 작성한 하나미 소개 포스트를 참고해주세요.</a></p>\n<p>엔티티 클래스 파일의 위치는 <code>lib/moneybook_api/entities</code> 입니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># lib/moneybook_api/entities/user.rb</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">User</span> <span class=\"token operator\">&lt;</span> <span class=\"token constant\">Hanami</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Entity</span>\n  <span class=\"token keyword\">def</span> show_info\n    <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span>to_h<span class=\"token punctuation\">.</span>tap <span class=\"token punctuation\">{</span> <span class=\"token operator\">|</span>attributes<span class=\"token operator\">|</span> attributes<span class=\"token punctuation\">.</span><span class=\"token function\">delete</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:password_digest</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">}</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>이렇게 하여 사용자 생성 후 리턴 값에는 패스워드를 뺸 나머지 정보만 보이게 됩니다.</p>\n<h3 id=\"토큰-생성하기\"><a href=\"#%ED%86%A0%ED%81%B0-%EC%83%9D%EC%84%B1%ED%95%98%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>토큰 생성하기</h3>\n<p>이제부터가 본격적인 JWT 인증을 구현하는 부분입니다. 생성한 사용자가 로그인을 시도하고 요청한 이메일과 비밀번호가 맞다면 우리는 인증 토큰을 생성하여 돌려주게 되고, 사용자는 매번 로그인 할 필요 없이 그 토큰을 가지고 (유효기간이 만료할 때 까지) 자유로이 앱을 사용할 수 있게 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/controllers/auth/sign_in.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Auth</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">SignIn</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n\n    <span class=\"token constant\">VALID_EMAIL_REGEX</span> <span class=\"token operator\">=</span> <span class=\"token regex\">/\\A[\\w+\\-.]+@[a-z\\d\\-]+(\\.[a-z\\d\\-]+)*\\.[a-z]+\\z/i</span>\n\n    params <span class=\"token keyword\">do</span>\n      <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:user</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>schema <span class=\"token keyword\">do</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:email</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">,</span> format<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> <span class=\"token constant\">VALID_EMAIL_REGEX</span><span class=\"token punctuation\">)</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:password</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      halt <span class=\"token number\">422</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Invalid Parameters'</span> <span class=\"token keyword\">unless</span> params<span class=\"token punctuation\">.</span>valid<span class=\"token operator\">?</span>\n      email <span class=\"token operator\">=</span> params<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:user</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:email</span><span class=\"token punctuation\">)</span>\n      password <span class=\"token operator\">=</span> params<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:user</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:password</span><span class=\"token punctuation\">)</span>\n\n      user <span class=\"token operator\">=</span> <span class=\"token constant\">UserRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">.</span><span class=\"token function\">find_by_email</span><span class=\"token punctuation\">(</span>email<span class=\"token punctuation\">)</span>\n\n      <span class=\"token keyword\">if</span> user <span class=\"token operator\">&amp;&amp;</span> user<span class=\"token punctuation\">.</span>valid_password<span class=\"token operator\">?</span><span class=\"token punctuation\">(</span>password<span class=\"token punctuation\">)</span>\n        token <span class=\"token operator\">=</span> <span class=\"token constant\">JwtIssuer</span><span class=\"token punctuation\">.</span><span class=\"token function\">encode</span><span class=\"token punctuation\">(</span>user<span class=\"token punctuation\">.</span>id<span class=\"token punctuation\">)</span>\n\n        status <span class=\"token number\">201</span><span class=\"token punctuation\">,</span> token<span class=\"token punctuation\">.</span>to_json\n      <span class=\"token keyword\">else</span>\n        halt <span class=\"token number\">401</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Authentication Failed'</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>유효성 검사를 하는 부분은 회원 등록 하는 액션에서도 적어놓았기 때문에 쉽게 알아보실 수 있을겁니다. 바로 아래에 <code>params.get</code> 이라는 메서드는 <code>Hash#dig</code> 메서드와 비슷한 용도를 가지고 있는데 <code>params[:user][:email]</code> 같이 값을 검색하려다가 <code>params[:user]</code> 가 <code>nil</code> 인 경우 <code>NoMethodError</code> 에러가 발생하는 일을 방지해줍니다. 대신 <code>nil</code> 을 리턴합니다.</p>\n<p><code>find_by_email</code> 같은 메서드는 레일즈를 하시는 분들이 보기엔 아주 친숙할겁니다. 하지만 우리는 이 메서드를 직접 구현해야 합니다. 레일즈로 개발하면서 느끼는 마법은 하나미에서는 일어나지 않습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># lib/moneybook_api/repositories/user_repository.rb</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">UserRepository</span> <span class=\"token operator\">&lt;</span> <span class=\"token constant\">Hanami</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Repository</span>\n  <span class=\"token keyword\">def</span> <span class=\"token function\">find_by_email</span><span class=\"token punctuation\">(</span>email<span class=\"token punctuation\">)</span>\n    users<span class=\"token punctuation\">.</span><span class=\"token function\">where</span><span class=\"token punctuation\">(</span>email<span class=\"token punctuation\">:</span> email<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">as</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:entity</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>one\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p><strong>모든 하나미의 쿼리 메서드는 private 입니다. 콘솔에서 바로 테스트 할 수 없습니다.</strong> 테스트를 작성하거나, 원하는 쿼리를 메서드로 정의한 뒤에 콘솔에서 돌려봐야 합니다.</p>\n<p><code>users</code> 는 리퍼지토리와 연결되어있는 테이블명을 나타내는 규약입니다. 만약 <code>BookRepository</code> 에서 <code>books</code> 라는 테이블의 내용을 가져오게 된다면 우리는 <code>books.</code> 로 시작하는 메서드를 작성하게 됩니다. 자세한 내용은 하나미 모델의 구현체인 <a href=\"http://rom-rb.org\">ROM</a>의 문서를 참고하시는게 좋습니다.</p>\n<p>레일즈를 개발하는 분들은 보통 쿼리의 결과가 각 모델의 객체로 나오기 때문에 이후에 각 엔티티마다 메서드를 바로 사용할 수 있을겁니다. 하지만 하나미에서는 <code>as(:entity)</code> 옵션을 적용해주지 않으면 결과물이 단순한 <code>ROM::Struct</code> 객체를 리턴하거나. <code>ROM::Relation</code> 객체를 리턴하는 경우가 많습니다.</p>\n<p><code>Struct</code> 객체는 Hash처럼 되어있어서, 쿼리의 결과 레코드를 그대로 쓰는 건 가능하지만 엔티티에 정의해놓은 메서드를 사용할 수 없습니다. <code>Relation</code> 객체는 쿼리를 아직 실행하기 전에 ROM에서 쿼리를 준비해 놓는 객체입니다. 이 객체에 <code>to_a</code> 나 <code>each</code> 같은 메서드를 사용해서 자료를 가공할 때 쿼리를 실행하고, 그 때 엔티티를 다룰 수 있습니다.</p>\n<p>지금은 간단한 쿼리라서 <code>.as(:entity)</code> 부분을 빼도 결과값을 맨 앞 한개만 출력하라는 <code>one</code> 메서드 때문에 바로 User 엔티티가 출력될겁니다. 이후에 더 복잡한 쿼리를 작성하게 되는 일이 있을 때 제가 말씀드린 내용이 도움이 되었으면 좋겠습니다.</p>\n<p><code>user.valid_password?</code> 메서드는 <code>BCrypt</code> 젬을 이용하여 암호화했던 비밀번호를 해독하고, 맞는지 확인하는 역할을 합니다.</p>\n<p><code>JwtIssuer</code> 클래스는 라이브러리로 활용하기 위해 <code>lib/moneybook_api/services</code> 폴더에 작성했습니다. 여기에 작성해 두면 다른 앱이나 액션에서 자유롭게 사용할 수 있기 때문입니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># lib/moneybook_api/services</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">JwtIssuer</span>\n  <span class=\"token constant\">DEFAULT_EXPIRE_TIME</span> <span class=\"token operator\">=</span> <span class=\"token number\">86400</span> <span class=\"token comment\"># 24 hours</span>\n\n  <span class=\"token keyword\">def</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span><span class=\"token function\">encode</span><span class=\"token punctuation\">(</span>user_id<span class=\"token punctuation\">,</span> exp <span class=\"token operator\">=</span> <span class=\"token builtin\">Time</span><span class=\"token punctuation\">.</span>now<span class=\"token punctuation\">.</span>to_i <span class=\"token operator\">+</span> <span class=\"token constant\">DEFAULT_EXPIRE_TIME</span><span class=\"token punctuation\">)</span>\n    payload <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span> user_id<span class=\"token punctuation\">:</span> user_id<span class=\"token punctuation\">,</span> iss<span class=\"token punctuation\">:</span> <span class=\"token constant\">ENV</span><span class=\"token punctuation\">[</span><span class=\"token string\">'JWT_ISSUER'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> exp<span class=\"token punctuation\">:</span> exp <span class=\"token punctuation\">}</span>\n    token <span class=\"token operator\">=</span> <span class=\"token constant\">JWT</span><span class=\"token punctuation\">.</span><span class=\"token function\">encode</span><span class=\"token punctuation\">(</span>payload<span class=\"token punctuation\">,</span> <span class=\"token constant\">ENV</span><span class=\"token punctuation\">[</span><span class=\"token string\">'JWT_SECRET'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'HS256'</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">{</span> auth_token<span class=\"token punctuation\">:</span> token <span class=\"token punctuation\">}</span>\n  <span class=\"token keyword\">end</span>\n\n  <span class=\"token keyword\">def</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span><span class=\"token function\">decode</span><span class=\"token punctuation\">(</span>token<span class=\"token punctuation\">)</span>\n    <span class=\"token constant\">JWT</span><span class=\"token punctuation\">.</span><span class=\"token function\">decode</span><span class=\"token punctuation\">(</span>token<span class=\"token punctuation\">,</span> <span class=\"token constant\">ENV</span><span class=\"token punctuation\">[</span><span class=\"token string\">'JWT_SECRET'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> <span class=\"token keyword\">true</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">[</span><span class=\"token number\">0</span><span class=\"token punctuation\">]</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>코드의 내용은 <a href=\"https://github.com/jwt/ruby-jwt/blob/master/README.md#algorithms-and-usage\">JWT 젬의 사용법을 따라</a> 우리가 필요한 부분을 캡슐화한 정도입니다. 토큰 생성 시점에서 24시간의 기한을 주었고, <code>iss</code> 옵션은 발행자 옵션인데, 임의로 <code>localhost</code> 로 지정해두었습니다. <code>ENV['JWT_SECRET']</code> 같이 지정되어있는 환경변수를 가져오는 방법은 프로젝트 폴더에 <code>.env.development</code> 같이 되어있는 파일에다 작성는 것입니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code># .env.development\n# ...\nJWT_SECRET=\"some secret\"\n# ...</code></pre>\n      </div>\n<p>비밀번호 토큰은 여러분이 원하는대로 만든 다음에, <code>Base64</code> 나 <code>Digest</code> 클래스를 활용하여 암호화하시길 추천합니다.</p>\n<p>인코딩에 성공하면 컨트롤러 액션은 201 스테이터스와 함께 토큰 JSON을 리턴하게 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ curl -X POST -H \"Content-Type: application/json\" -H \"Accept: application/json\" -d '{ \"user\": { \"email\": \"foo@bar.com\", \"foo\": \"cherryblossom\" } }' \"http://localhost:2300/api/auth/sign_in\"\n=>\n{\n  \"auth_token\": \"eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyX2lkIjo0LCJpc3MiOiJsb2NhbGhvc3QiLCJleHAiOjE0OTI5NjM2NTV9.38-CWKZkOuWD_idEOhv1gAret6_qG_ue7kk0iPftJ_M\"\n}</code></pre>\n      </div>\n<p>이제 사용자가 매 요청을 할 때마다 이 토큰이 유효한지 검사하는 부분을 만들어야 하는데, 가계부 기록 작성과 관련된 API를 구현한 뒤에 마저 만들어 보겠습니다.</p>\n<h2 id=\"가계부-api-만들기\"><a href=\"#%EA%B0%80%EA%B3%84%EB%B6%80-api-%EB%A7%8C%EB%93%A4%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>가계부 API 만들기</h2>\n<p>가계부 API는 간단한 REST 형식으로 만들 예정입니다.</p>\n<table>\n<thead>\n<tr>\n<th align=\"center\">정의</th>\n<th align=\"center\">내용</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">/records (GET)</td>\n<td align=\"center\">모든 레코드를 가져온다</td>\n</tr>\n<tr>\n<td align=\"center\">/records (POST)</td>\n<td align=\"center\">레코드를 생성한다</td>\n</tr>\n<tr>\n<td align=\"center\">/records/:record_id (PATCH)</td>\n<td align=\"center\">해당 레코드를 수정한다</td>\n</tr>\n<tr>\n<td align=\"center\">/records/:record_id(DELETE)</td>\n<td align=\"center\">해당 레코드를 삭제한다</td>\n</tr>\n</tbody>\n</table>\n<p>RESTful API 라면 보통 사용자가 가지고 있는 레코드를 표현해야 하니까 URL 어딘가에 <code>user_id</code> 같은게 있어야 할 것 처럼 보입니다. 하지만 우리가 구현하고자 하는 서버에는 필요가 없습니다. JWT 토큰을 HTTP 헤더에 추가하여 요청을 하면, 서버가 토큰을 해석하여 <code>user_id</code> 를 찾아내고 그 값을 기반으로 특정 사용자의 레코드를 검색하게 됩니다.</p>\n<h3 id=\"모델-생성--라우팅\"><a href=\"#%EB%AA%A8%EB%8D%B8-%EC%83%9D%EC%84%B1--%EB%9D%BC%EC%9A%B0%ED%8C%85\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>모델 생성 &#x26; 라우팅</h3>\n<p>일단 레코드 모델과 레코드를 생성하는 액션을 만들고 나서, 토큰 인증 과정을 덧붙이도록 하겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>bundle exec hanami generate model record</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># db/migrations/2017**_create_records.rb</span>\n<span class=\"token constant\">Hanami</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Model</span><span class=\"token punctuation\">.</span>migration <span class=\"token keyword\">do</span>\n  change <span class=\"token keyword\">do</span>\n    create_table <span class=\"token symbol\">:records</span> <span class=\"token keyword\">do</span>\n      primary_key <span class=\"token symbol\">:id</span>\n      foreign_key <span class=\"token symbol\">:user_id</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:users</span><span class=\"token punctuation\">,</span> on_delete<span class=\"token punctuation\">:</span> <span class=\"token symbol\">:cascade</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n\n      column <span class=\"token symbol\">:amount</span><span class=\"token punctuation\">,</span>      <span class=\"token builtin\">Integer</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n      column <span class=\"token symbol\">:description</span><span class=\"token punctuation\">,</span> <span class=\"token builtin\">String</span>\n\n      column <span class=\"token symbol\">:created_at</span><span class=\"token punctuation\">,</span> <span class=\"token constant\">DateTime</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n      column <span class=\"token symbol\">:updated_at</span><span class=\"token punctuation\">,</span> <span class=\"token constant\">DateTime</span><span class=\"token punctuation\">,</span> null<span class=\"token punctuation\">:</span> <span class=\"token keyword\">false</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>외부 키는 컬럼 이름과, 연결해 줄 테이블 이름을 지정해줍니다. 나머지 뒤에 있는 요소들은 <a href=\"http://hanamirb.org/guides/migrations/create-table/\">공식 문서에 자세한 설명이 있으니</a> 참조바랍니다. 그리고 마이그레이션을 실행하면 됩니다.</p>\n<p>그 다음에 액션을 생성하고 라우트를 지정하겠습니다. 다른 <code>records</code> 관련 액션을 미리 생성해두셔도 상관 없습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>bundle exec hanami generate action api records#create --skip-view</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/config/routes.rb</span>\n<span class=\"token comment\"># ...</span>\nresources <span class=\"token symbol\">:records</span><span class=\"token punctuation\">,</span> only<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token symbol\">:index</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:create</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:update</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:destroy</span><span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>\n<p>이 부분은 레일즈의 자동 라우팅과 아주 유사합니다. 라우팅이 어떻게 구성되었는지 확인하시려면 <code>bundle exec hanami routes</code> 커맨드를 실행하시면 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>    Name Method     Path                           Action\n\n         POST       /api/auth/sign_in    Api::Controllers::Auth::SignIn\n         POST       /api/auth/sign_up    Api::Controllers::Auth::SignUp\n records GET, HEAD  /api/records         Api::Controllers::Records::Index\n records POST       /api/records         Api::Controllers::Records::Create\n  record PATCH      /api/records/:id     Api::Controllers::Records::Update\n  record DELETE     /api/records/:id     Api::Controllers::Records::Destroy</code></pre>\n      </div>\n<h3 id=\"레코드-생성\"><a href=\"#%EB%A0%88%EC%BD%94%EB%93%9C-%EC%83%9D%EC%84%B1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>레코드 생성</h3>\n<p>이제 세부 로직을 구현할 준비가 끝났으니 레코드 생성을 해 보겠습니다. 지금은 임의의 사용자를 지정하여 레코드를 생성하려 합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/records/create.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Records</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">Create</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n\n    params <span class=\"token keyword\">do</span>\n      <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:record</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>schema <span class=\"token keyword\">do</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:amount</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:int?</span><span class=\"token punctuation\">)</span>\n        <span class=\"token function\">optional</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:description</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      halt <span class=\"token number\">422</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Invalid Parameters'</span> <span class=\"token keyword\">unless</span> params<span class=\"token punctuation\">.</span>valid<span class=\"token operator\">?</span>\n      user <span class=\"token operator\">=</span> <span class=\"token constant\">UserRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">.</span>first\n      amount <span class=\"token operator\">=</span> params<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:record</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:amount</span><span class=\"token punctuation\">)</span>\n      description <span class=\"token operator\">=</span> params<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:record</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:description</span><span class=\"token punctuation\">)</span>\n\n      record <span class=\"token operator\">=</span> <span class=\"token constant\">RecordRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">.</span><span class=\"token function\">create</span><span class=\"token punctuation\">(</span>\n        user_id<span class=\"token punctuation\">:</span> user<span class=\"token punctuation\">.</span>id<span class=\"token punctuation\">,</span>\n        amount<span class=\"token punctuation\">:</span> amount<span class=\"token punctuation\">,</span>\n        description<span class=\"token punctuation\">:</span> description\n      <span class=\"token punctuation\">)</span>\n\n      status <span class=\"token number\">201</span><span class=\"token punctuation\">,</span> record<span class=\"token punctuation\">.</span>to_json\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>패러매터가 유효하면 레코드를 생성하고 그 정보를 JSON으로 리턴하는 간단한 내용의 코드입니다. 이제 패러매터의 유효성은 둘째치고 모든 액션에 앞서 토큰의 유효성을 검사하는 모듈이 필요합니다. 이 파일을 어디에 정의하고 어떻게 사용하는지 <a href=\"http://hanamirb.org/guides/actions/share-code/\">가이드에 잘 설명이 되어있는데</a>, 가이드의 내용을 활용하여 만들어보겠습니다.</p>\n<h3 id=\"토큰-인증\"><a href=\"#%ED%86%A0%ED%81%B0-%EC%9D%B8%EC%A6%9D\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>토큰 인증</h3>\n<p>컨트롤러에 공통적으로 사용되는 모듈은 해당 앱의 <code>/controllers</code> 폴더에다 바로 작성하면 됩니다. 그리고 모든 컨트롤러 액션에 공통적으로 사용하고자 하면 <code>application.rb</code> 파일을 수정해야 합니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/authentication.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span>\n  <span class=\"token keyword\">module</span> <span class=\"token constant\">Authentication</span>\n    <span class=\"token keyword\">module</span> <span class=\"token constant\">Skip</span>\n      <span class=\"token keyword\">def</span> authenticate<span class=\"token operator\">!</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span><span class=\"token function\">included</span><span class=\"token punctuation\">(</span>action<span class=\"token punctuation\">)</span>\n      action<span class=\"token punctuation\">.</span>class_eval <span class=\"token keyword\">do</span>\n        before <span class=\"token symbol\">:authenticate!</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    private\n\n    <span class=\"token keyword\">def</span> authenticate<span class=\"token operator\">!</span>\n      halt <span class=\"token number\">401</span> <span class=\"token keyword\">unless</span> authenticated<span class=\"token operator\">?</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> authenticated<span class=\"token operator\">?</span>\n      <span class=\"token operator\">!</span><span class=\"token operator\">!</span>current_user\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> current_user\n      user_id <span class=\"token operator\">=</span> validate_token <span class=\"token operator\">?</span> validate_token<span class=\"token punctuation\">[</span><span class=\"token string\">'user_id'</span><span class=\"token punctuation\">]</span> <span class=\"token punctuation\">:</span> <span class=\"token keyword\">nil</span>\n      <span class=\"token constant\">UserRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">.</span><span class=\"token function\">find</span><span class=\"token punctuation\">(</span>user_id<span class=\"token punctuation\">)</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> validate_token\n      token_header <span class=\"token operator\">=</span> request<span class=\"token punctuation\">.</span><span class=\"token function\">get_header</span><span class=\"token punctuation\">(</span><span class=\"token string\">'HTTP_AUTHORIZATION'</span><span class=\"token punctuation\">)</span>\n      token <span class=\"token operator\">=</span> token_header<span class=\"token punctuation\">.</span><span class=\"token function\">gsub</span><span class=\"token punctuation\">(</span><span class=\"token regex\">/^Bearer\\s/</span><span class=\"token punctuation\">,</span> <span class=\"token string\">''</span><span class=\"token punctuation\">)</span>\n\n      <span class=\"token keyword\">begin</span>\n        <span class=\"token constant\">JwtIssuer</span><span class=\"token punctuation\">.</span><span class=\"token function\">decode</span><span class=\"token punctuation\">(</span>token<span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">rescue</span>\n        <span class=\"token keyword\">nil</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>몇 가지 이상한 녀석들이 나왔습니다. 일단 <code>included</code> 메서드는 모듈이 특정 모듈이나 클래스에 <code>include</code> 될 때 실행되는 훅(Hook)과 같은 메서드입니다. 인자 <code>action</code> 은 이 모듈을 불러온 액션을 뜻합니다. 그리고 <code>class_eval</code> 은 해당 클래스에 블락의 내용을 추가로 작성해주는 메서드라고 이해하시면 됩니다. 즉 이 모듈을 불러오는 클래스는 <code>before :authenticate!</code> 라는 코드 라인이 추가되는겁니다. <code>before</code> 는 레일즈의 <code>before_action</code> 처럼 해당 액션을 실행하기 전에 먼저 실행되는 메서드를 정의합니다.</p>\n<p>마지막으로 <code>request</code> 는 <code>Rack::Request</code> 에 정의되어 있는 객체입니다 <a href=\"http://hanamirb.org/guides/actions/request-and-response/\">가이드에 간단한 사용법이 있지만</a> 더 자세한 사용법을 보시려면 <a href=\"http://www.rubydoc.info/gems/rack/Rack/Request\">직접 문서를 보셔야 합니다</a>.</p>\n<p>사용자는 발급받은 토큰을 매 HTTP 헤더에 실어서 보내야 합니다. 그 형식은 <code>Authorization => Bearer {token}</code> 이 되도록 지정했습니다.</p>\n<p>이제 애플리케이션 설정 파일에서 이 모듈을 공통적으로 불러오게 하겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/application.rb</span>\n<span class=\"token comment\"># ...</span>\nrequire_relative <span class=\"token string\">'./controllers/authentication'</span>\n<span class=\"token comment\"># ...</span>\n      controller<span class=\"token punctuation\">.</span>prepare <span class=\"token keyword\">do</span>\n        include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Authentication</span> <span class=\"token comment\"># included in all the actions</span>\n      <span class=\"token keyword\">end</span>\n<span class=\"token comment\"># ...</span>\n</code></pre>\n      </div>\n<p>약 260번째 줄 부근에 해당 코드가 있습니다. 모든 컨트롤러 액션에 이 블록 안의 코드가 추가됩니다. 맨 윗줄에 <code>require_relative</code> 로 파일을 불러오는 걸 잊어버리시면 안됩니다.</p>\n<p>그 다음 <code>records#create</code> 액션의 코드를 수정해줍시다. 한 줄이면 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/records/create.rb</span>\n<span class=\"token comment\"># ...</span>\nuser <span class=\"token operator\">=</span> current_user\n<span class=\"token comment\"># ...</span>\n</code></pre>\n      </div>\n<p>테스트를 잘 작성해오셨다면 전혀 뜬금없이 회원 가입, 로그인 액션에서 오류가 납니다. 인증 토큰을 요구할 필요가 없는 액션에서도 토큰을 요구하고 있기 때문입니다. 하지만 위에 인증을 스킵할 수 있도록 오버라이드용 메서드를 정의해 두었으니 <code>Api::Authentication::Skip</code> 모듈을 불러오기만 하면 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/auth/sign_up.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Auth</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">SignUp</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Authentication</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Skip</span>\n<span class=\"token comment\"># ...</span>\n</code></pre>\n      </div>\n<h3 id=\"나머지-액션들-작성\"><a href=\"#%EB%82%98%EB%A8%B8%EC%A7%80-%EC%95%A1%EC%85%98%EB%93%A4-%EC%9E%91%EC%84%B1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>나머지 액션들 작성</h3>\n<p>이제 레코드를 만들고, 인증하는 과정을 만들었으니 나머지 액션들은 비교적 간단합니다. 간단하게 작성한 코드를 참고해주세요.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/records/index.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Records</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">Index</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">initialize</span><span class=\"token punctuation\">(</span>repository<span class=\"token punctuation\">:</span> <span class=\"token constant\">RecordRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">)</span>\n      <span class=\"token variable\">@repo</span> <span class=\"token operator\">=</span> repository\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      status <span class=\"token number\">200</span><span class=\"token punctuation\">,</span> get_records<span class=\"token punctuation\">.</span>to_json\n    <span class=\"token keyword\">end</span>\n\n    private\n\n    attr_reader <span class=\"token symbol\">:repo</span>\n\n    <span class=\"token keyword\">def</span> get_records\n      records <span class=\"token operator\">=</span> repo<span class=\"token punctuation\">.</span><span class=\"token function\">by_user</span><span class=\"token punctuation\">(</span>current_user<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>map <span class=\"token punctuation\">{</span> <span class=\"token operator\">|</span>record<span class=\"token operator\">|</span> record<span class=\"token punctuation\">.</span>to_h <span class=\"token punctuation\">}</span>\n\n      <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">.</span>tap <span class=\"token keyword\">do</span> <span class=\"token operator\">|</span>hash<span class=\"token operator\">|</span>\n        hash<span class=\"token punctuation\">.</span><span class=\"token function\">store</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:records</span><span class=\"token punctuation\">,</span> records<span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p><code>by_user</code> 메서드는 <code>RecordRepository</code> 에 정의되어 있습니다. 사실 레일즈 사용자분들이라면 모델에 <code>has_many</code> 같은 메서드를 사용하여 1:N 관계를 정의하는 경우가 보편적일텐데요. 하나미도 <a href=\"http://hanamirb.org/guides/models/associations/\">최근에 그런 기능을 지원하기 시작헀지만</a> 아직 실험적인 기능이기 때문에 직접 구현했습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># lib/moneybook_api/repositories/record_repository.rb</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">RecordRepository</span> <span class=\"token operator\">&lt;</span> <span class=\"token constant\">Hanami</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Repository</span>\n  <span class=\"token keyword\">def</span> <span class=\"token function\">by_user</span><span class=\"token punctuation\">(</span>user<span class=\"token punctuation\">)</span>\n    records<span class=\"token punctuation\">.</span><span class=\"token function\">where</span><span class=\"token punctuation\">(</span>user_id<span class=\"token punctuation\">:</span> user<span class=\"token punctuation\">.</span>id<span class=\"token punctuation\">)</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>이후의 결과물은 완성된 Hash나 Array가 아니라 <code>Enumerator</code> 같은 객체라서 원하는 결과물을 반복 메서드를 사용하여 다양하게 다룰 수 있습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code>require_relative <span class=\"token string\">'./set_record'</span>\n\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Records</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">Update</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n    include <span class=\"token constant\">SetRecord</span>\n\n    params <span class=\"token keyword\">do</span>\n      <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:record</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span>schema <span class=\"token keyword\">do</span>\n        <span class=\"token function\">required</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:amount</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:int?</span><span class=\"token punctuation\">)</span>\n        <span class=\"token function\">optional</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:description</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">filled</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:str?</span><span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">initialize</span><span class=\"token punctuation\">(</span>repository<span class=\"token punctuation\">:</span> <span class=\"token constant\">RecordRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">)</span>\n      <span class=\"token variable\">@repo</span> <span class=\"token operator\">=</span> repository\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      halt <span class=\"token number\">422</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'Invalid Parameters'</span> <span class=\"token keyword\">unless</span> params<span class=\"token punctuation\">.</span>valid<span class=\"token operator\">?</span>\n\n      <span class=\"token keyword\">begin</span>\n        updated_record <span class=\"token operator\">=</span> repo<span class=\"token punctuation\">.</span><span class=\"token function\">update</span><span class=\"token punctuation\">(</span>record<span class=\"token punctuation\">.</span>id<span class=\"token punctuation\">,</span> params<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token symbol\">:record</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n        status <span class=\"token number\">200</span><span class=\"token punctuation\">,</span> updated_record<span class=\"token punctuation\">.</span>to_h<span class=\"token punctuation\">.</span>to_json\n      <span class=\"token keyword\">rescue</span>\n        status <span class=\"token number\">400</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> error<span class=\"token punctuation\">:</span> <span class=\"token string\">'There was a problem during the process'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">.</span>to_json\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    private\n\n    attr_reader <span class=\"token symbol\">:repo</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:record</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p><code>SetRecord</code> 모듈도 <a href=\"http://hanamirb.org/guides/actions/share-code/\">공식 가이드의 내용</a>을 참고하여 만들었습니다. Update와 Destroy 액션에서 공통적으로 레코드를 찾는 과정이 필요한데, 모듈에다 구현해두고 여러 액션에서 재활용할 수 있도록 만든겁니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/records/set_record.rb</span>\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Records</span>\n  <span class=\"token keyword\">module</span> <span class=\"token constant\">SetRecord</span>\n    <span class=\"token keyword\">def</span> <span class=\"token keyword\">self</span><span class=\"token punctuation\">.</span><span class=\"token function\">included</span><span class=\"token punctuation\">(</span>action<span class=\"token punctuation\">)</span>\n      action<span class=\"token punctuation\">.</span>class_eval <span class=\"token keyword\">do</span>\n        before <span class=\"token symbol\">:set_record</span>\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    private\n\n    <span class=\"token keyword\">def</span> set_record\n      <span class=\"token variable\">@record</span> <span class=\"token operator\">=</span> <span class=\"token constant\">RecordRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">.</span><span class=\"token function\">find</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">[</span><span class=\"token symbol\">:id</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span>\n      halt <span class=\"token number\">404</span> <span class=\"token keyword\">unless</span> <span class=\"token variable\">@record</span>\n    <span class=\"token keyword\">end</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token comment\"># apps/api/controllers/records/destroy.rb</span>\nrequire_relative <span class=\"token string\">'./set_record'</span>\n\n<span class=\"token keyword\">module</span> <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Controllers</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Records</span>\n  <span class=\"token keyword\">class</span> <span class=\"token class-name\">Destroy</span>\n    include <span class=\"token constant\">Api</span><span class=\"token punctuation\">:</span><span class=\"token symbol\">:Action</span>\n    include <span class=\"token constant\">SetRecord</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">initialize</span><span class=\"token punctuation\">(</span>repository<span class=\"token punctuation\">:</span> <span class=\"token constant\">RecordRepository</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">)</span>\n      <span class=\"token variable\">@repo</span> <span class=\"token operator\">=</span> repository\n    <span class=\"token keyword\">end</span>\n\n    <span class=\"token keyword\">def</span> <span class=\"token function\">call</span><span class=\"token punctuation\">(</span>params<span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">begin</span>\n        repo<span class=\"token punctuation\">.</span><span class=\"token function\">delete</span><span class=\"token punctuation\">(</span>record<span class=\"token punctuation\">.</span>id<span class=\"token punctuation\">)</span>\n        status <span class=\"token number\">204</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> success<span class=\"token punctuation\">:</span> <span class=\"token string\">'Successfully Removed'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">.</span>to_json\n      <span class=\"token keyword\">rescue</span>\n        status <span class=\"token number\">400</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> error<span class=\"token punctuation\">:</span> <span class=\"token string\">'There is a problem during the process'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">.</span>to_json\n      <span class=\"token keyword\">end</span>\n    <span class=\"token keyword\">end</span>\n\n    private\n\n    attr_reader <span class=\"token symbol\">:repo</span><span class=\"token punctuation\">,</span> <span class=\"token symbol\">:record</span>\n  <span class=\"token keyword\">end</span>\n<span class=\"token keyword\">end</span>\n</code></pre>\n      </div>\n<p>축하합니다. 여러분은 하나미로 간단 API 서버를 가독성 높고 모듈화하기 용이한 루비 코드로 작성하였습니다.</p>\n<hr>\n<p>사실 더 개선할 부분들은 많습니다. 예를 들면 Interactor 를 활용한 로직 분리, 명확한 의존성 주입으로 테스트 효율 높이기 + 더 짜임새 있는 테스트 작성 등을 고려해야겠지요. 마침 <a href=\"https://github.com/nickgnd/hanami-jwt-example\">비슷한 내용의 예제 코드가 아주아주 높은 퀄리티로 작성되어 있어서</a> 이번 튜토리얼을 구현해보면서 기본 개념을 익히신 다음에, 다른 예제를 참고하시면 큰 도움이 될 것이라 생각합니다.</p>\n<ul>\n<li>Source Code: <a href=\"https://github.com/emaren84/moneybook_api\">Github</a></li>\n</ul>","frontmatter":{"date":"2017/04/27","path":"/posts/hanami-vuejs-moneybook-api-server","title":"Hanamirb + Vue.js 로 간단 가계부 구현 (API 서버 편)","tags":["Ruby","Hanami","Vue.js"]}},"site":{"siteMetadata":{"siteUrl":"https://emaren84.github.io","disqusShortUrl":"gatsby-blog-1"}}},"pathContext":{}}

/***/ })

});


